import { History, LocationState } from 'history';
import { connectRouter, LocationChangeAction } from 'connected-react-router';

import { AppReducer } from '../../constants';

export const getRouterModel = (history: History): AppReducer => {
    const changeRouter = connectRouter<LocationState>(history);

    return (state, action) => {
        /**
         * @see https://github.com/supasate/connected-react-router/blob/master/src/reducer.js#L58
         *    ,    
         *   .   ,    ,
         *    LocationChangeAction (    ).
         *
         *   ,         ,
         *    @@INIT       router.
         */
        state.router = changeRouter(state.router, action as LocationChangeAction);
    };
};
